import Layout from '../components/Layout';

export default () => (
	<Layout>
		<div>
			<div className="has-background-white">
				<br />
				<div className="columns">
					<div className="column"></div>
					<div className="column is-5">
						<p className="title has-text-link has-text-centered">Get started with Xoyondo</p>
						<p className="subtitle has-text-centered">It's free and takes only 12 seconds.</p>
						<div className="box" style={{backgroundColor:"#C8E4FF"}}>
							<div className="field">
								<label className="label">Name</label>
								<div className="control">
									<input className="input" type="text" placeholder="Text input" />
								</div>
							</div>

							<div className="field">
								<label className="label">Username</label>
								<div className="control has-icons-left has-icons-right">
									<input
										className="input is-success"
										type="text"
										placeholder="Text input"
									/>
									<span className="icon is-small is-left">
										<i className="fas fa-user"></i>
									</span>
									<span className="icon is-small is-right">
										<i className="fas fa-check"></i>
									</span>
								</div>
								{/* <p className="help is-dark">This username is available</p> */}
							</div>

							<div className="field">
								<label className="label">Email</label>
								<div className="control has-icons-left has-icons-right">
									<input
										className="input"
										type="email"
										placeholder="Email input"
									/>
									<span className="icon is-small is-left">
										<i className="fas fa-envelope"></i>
									</span>
									<span className="icon is-small is-right">
										<i className="fas fa-exclamation-triangle"></i>
									</span>
								</div>
								{/* <p className="help is-danger">This email is invalid</p> */}
							</div>

							<div className="field is-grouped">
								<div className="control">
									<button className="button is-link">Submit</button>
								</div>
								<div className="control">
									<button className="button is-link is-light">Cancel</button>
								</div>
							</div>
						</div>
					</div>
					<div className="column"></div>
				</div>
			</div>
			<br />
			<br />
		</div>
	</Layout>
);
